import React, { useState, useEffect } from 'react';

import Breakpoint from '../components/breakpoint/breakpoint'
const Search = (props) => {

    const [query, setQuery] = useState("")
    useEffect(() => {

        let q = props.data.location.search.replace("?", "")
        q = q.split("&")
        q.forEach((item) => {
            let i = item.split("=")
            if (i[0] === "q") {
                setQuery(i[1])
            }
        })

        // return () => {
        //     // cleanup
        // }
    }, [props.data])
    return (
        <>
            <Breakpoint xs sm>
                <br />
                <br />
            </Breakpoint>

            <div className="container">
                <h1>Search:</h1>
                {query}

            </div>
        </>
    );
}

export default Search;